.CreateTodoButton {
    background-color: #009947;
    /* box-shadow: 0px 5px 5px rgba(97, 218, 250, 0.5); */
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    /* El elemento se queda ubicado sin importar el scroll */
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    transform: rotate(0);
    transition: .3s ease;
    /* El elemento se mantiene siempre encima de todo */
    z-index: 1;
  }
  
  .CreateTodoButton:hover {
    transform: rotate(224deg);
  }